.game {
    width: 100%;
    margin: 20vh auto 0;
}

.game-board {
    display:flex;
    flex-direction: row;
    /* height: 100vh; */
    justify-content: center;
    align-items: center;
    

}

.game-info {
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.m-button {
    width: 200px;
    font-size: 20px;
    font-family: 'Play', sans-serif;
    background: violet;
    color: #fff;
    border: 1px solid #fff;
    padding: 5px;
    border-radius: 5px;
    box-shadow: 1px 1px 5px 2px#DF01D7;
}

.player-name {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: whitesmoke;
}